body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

h3, h4 {
    margin: 0;
}

h3{
    font-weight: 500;
}

h4 {
    font-weight: 400;
}

.line {
    height: 1px;
    width: 300px;
    background-color: #5827E3;
    margin: 10px 0;
}

.contact-detail {
    display: flex;
}

.mobile-number, .website-name {
    display: flex;
}

.mobile-number .icon-container, .website-name .icon-container {
    height: 3vh;
    object-fit: contain;
}

.mobile-number .icon-container img, .website-name .icon-container img {
    height: 100%;
    width: auto;
}

.mobile-number, .website-name {
    align-items: center;
}

.website-name, .icon-container {
    margin: 0 10px;
}

.mobile-number .detail-container h3 {
    font-weight: 400;
    font-size: 14px;
}

.website-name .detail-container a {
    text-decoration: none;
    color: #000;
} 

.social-icons a img {
    width: 3%;
    margin: 10px 5px;
}
